
.product-of-the-week {
  background-color: @white;
  position: relative;
  overflow: hidden;
}

.product-of-the-week__wrapper {
  .wrapper ();
  box-sizing: border-box;
  position: relative;


  @media (min-width: @tablets-width) {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: auto auto;
    column-gap: 42px;
    margin-bottom: 69px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 119px 292px 1fr;
    grid-template-rows: 154px auto auto;

    column-gap: 55px;

    margin-bottom: 96px;
  }
  margin-bottom: 42px;

  &::after {
    content: "";
    position: absolute;
    left: -1000px;
    width: 3000px;
    height: 6px;
    background-position: 0 6px;
    background-image: url("../img/svg/mobile-wave-second.svg");
    bottom: -47px;

    @media (min-width: @tablets-width) {
      background-image: url("../img/svg/tablets-wave-second.svg");
      height: 17px;
      background-position: 0 17px;
      bottom: -69px;
      left: -995px;
    }

    @media (min-width: @desktop-width) {
      bottom: -96px;
      background-image: url("../img/svg/desktop-wave-second.svg");
      height: 23px;
      background-position: 0 23px;
      left: -1010px;
    }
  }
}

.product-of-the-week__product-title {
  .text-settings();
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: @dark;
  z-index: 10;

  margin: 0;
  margin-bottom: 5px;

  @media (min-width: @tablets-width) {
    font-size: 43px;
    line-height: 50px;

    grid-column: 1/3;
    grid-row: 1/2;
    margin-bottom: 37.5px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
    grid-column: 2/4;

    margin-top: 61px;
    margin-bottom: 35px;

    height: fit-content;
  }
}

.product-of-the-week__title {
  .text-settings();
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: @aqua;

  margin: 0;
  margin-bottom: 17.5px;

  @media (min-width: @tablets-width) {
    line-height: 17px;
    margin-right: 27px;
    margin-left: auto;
    text-align: center;
    margin-top: 6px;

    grid-column: 3/4;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1/2;
    margin-right: 0;
    margin-left: auto;
    margin-top: 68px;
    margin-bottom: 0;

    height: fit-content;
  }
}

.product-of-the-week__description {
  .text-settings();
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: @grey;

  margin: 0;
  margin-bottom: 28px;

  @media (min-width: @tablets-width) {
    margin-bottom: 48px;
    font-size: 17px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 60px;
  }
}

.product-of-the-week__list {
  margin: 0;
}

.product-of-the-week__characteristic {
  .text-settings();
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: @grey;
}

.product-of-the-week__value {
  .text-settings();
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: @grey;

  margin: 0;
}

.product-of-the-week__price {
  .text-settings();
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: @grey;

  margin: 0;
  margin-bottom: 18.5px;

  text-align: center;

  @media (min-width: @tablets-width) {
    font-size: 20px;
    margin-bottom: 33px;
  }
}

.product-of-the-week__item {
  display: grid;
  grid-template-columns: 70px 1fr;
  column-gap: 28px;
  padding-bottom: 13px;
  border-bottom: 1px solid @grey-light;

  margin-bottom: 5px;

  @media (min-width: @desktop-width) {
    padding-bottom: 15px;
  }
}

.product-of-the-week__text-container {
  margin-bottom: 28px;

  @media (min-width: @tablets-width) {
    grid-column: 1/2;
    grid-row: 2/3;
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2/3;
    margin-bottom: 100px;
  }
}

.product-of-the-week__price-button-container {
  @media (min-width: @tablets-width) {
    grid-column: 1/2;
    grid-row: 3/4;
    align-self: flex-end;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2/3;
  }
}

.product-of-the-week__img {
  margin-bottom: 25.5px;

  @media (min-width: @tablets-width) {
    grid-column: 2/4;
    grid-row: 2/4;
    margin-top: 6px;
    margin-bottom: 0;

    width: 334px;
    height: 404px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 3/4;
    grid-row: 1/4;

    width: 525px;
    height: 636px;
    margin-top: 0;
  }
}

.product-of-the-week__br {
  display: none;
  margin: 0;
  padding: 0;
  position: absolute;

  @media (min-width: @tablets-width) {
    display: block;
  }
}
