.main-form {
  background-color: @white;
}

.main-form__title-container {
  background-color: @light-white;
  text-align: center;

  margin-bottom: 18px;

  overflow: hidden;

  @media (min-width: @tablets-width) {
    margin-bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 95px;
  }
}

.main-form__title-wrapper {
  .wrapper ();
  position: relative;
  box-sizing: border-box;

  padding: 73px 0;

  @media (min-width: @tablets-width) {
    padding: 55px 0 87px 0;

    &::before {
      content: "";
      background-image: url("../img/svg/tablets-wave.svg");
      position: absolute;
      height: 34px;
      width: 3000px;
      bottom: -10px;
      left: -500px;
      z-index: 10;
      background-repeat: repeat-x;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 60px 0 113px 0;

    &::before {
      content: "";
      background-image: url("../img/svg/desktop-wave.svg");
      position: absolute;
      height: 34px;
      width: 3000px;
      bottom: 0;
      left: -500px;
      z-index: 10;
      background-repeat: repeat-x;
    }
  }
}


.main-form__title {
  .text-settings();
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: @dark;

  margin: 0;

  @media (min-width: @tablets-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}
